.card {
  width: 200px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 16px 16px ;
  transition: all 300ms;

  &-picture {
    display: block;
    width: 160px;
    height: 134px;
    margin: 0 auto;
  }

  &-content {
    width: 166px;
    margin-top: 40px;

    &__info {
      margin-bottom: 16px;

      &-name {
        font-weight: 500;
        color: $main-text-color;
        margin-bottom: 4px;
      }

      &-code {
        font-size: 10px;
        font-weight: 400;
        line-height: 14px;
        color: $secondary-text-color;
      }
    }

    &__stars {
      @include centeringBlocks;
      margin-bottom: 24px;
    }

    &__reviews {
      font-size: 10px;
      font-weight: 400;
      line-height: 14px;
    }

    &__price {
      @include centeringBlocks;
      margin-bottom: 16px;

      &-price {
        font-weight: 400;
        color: $secondary-text-color;
      }

      &-pricevalue {
        font-size: 16px;
        font-weight: 700;
        line-height: 18px;
        color: $main-text-color;
      }
    }

    &__button {
      display: block;
      height: 40px;
      width: 100%;
      text-decoration: none;
      background-color: $main-color;
      color: #fff;
      border-radius: 5px;
      text-transform: uppercase;
      border: 1px solid transparent;

      font-size: 14px;
      font-weight: 700;
      line-height: 16px;
      text-align: center;
      padding-top: 13px;

      &:hover {
        cursor: pointer;
        color: $main-color;
        background-color: #fff;
        border: 1px solid $main-color;
      }
    }
  }

  &:hover {
    transform: scale(1.2);
  }

  &:hover &-content__info-name {
    color: #34568b ;
  }
}
